import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import './plugins/element.js'
import axios from 'axios'
//  nprogress  jscss
import Nprogress from 'nprogress'
import 'nprogress/nprogress.css'

// 
import '@/assets/css/global.css'

// 
import '@/assets/fonts/iconfont.css'

// 
axios.defaults.baseURL = 'http://127.0.0.1:4000/open'
// axiosrequesttoken,(token) Nprogress  Nprogress.start()
axios.interceptors.request.use(config => {
  // console.log(config)
  Nprogress.start() // 
  config.headers.Authorization = window.sessionStorage.getItem('token')
  //  return config
  return config
})

//  response Nprogress.done()
axios.interceptors.response.use(config => {
  Nprogress.done() // 
  return config
})

Vue.prototype.$http = axios

Vue.config.productionTip = false

// vue(vue) 
Vue.filter('dateFormat', function (originVal) {
  const dt = new Date(originVal * 1000)
  const y = dt.getFullYear() // 
  const m = (dt.getMonth() + 1 + '').padStart(2, '0') // 
  const d = (dt.getDate() + '').padStart(2, '0') // 

  const hh = (dt.getHours() + '').padStart(2, '0') // 
  const mm = (dt.getMinutes() + '').padStart(2, '0') // 
  const ss = (dt.getSeconds() + '').padStart(2, '0') // 
  return `${y}-${m}-${d} ${hh}:${mm}:${ss}`
})

Vue.filter('dateBirth', function (originVal) { // 
  const dt = new Date(originVal * 1000)
  const y = dt.getFullYear() // 
  const m = (dt.getMonth() + 1 + '').padStart(2, '0') // 
  const d = (dt.getDate() + '').padStart(2, '0') // 
  return `${y}${m}${d}`
})

Vue.filter('sexConvert', function (data) { // 
  if (data === 0) {
    return ''
  } else {
    return ''
  }
})

Vue.filter('typeConvert', function (data) { // 
  if (data === 0) {
    return ''
  } else {
    return ''
  }
})

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
